


import React from "react";
import Link from 'gatsby-link';
import PostList from './../components/Blog/PostList.js';
// let path = `${process.env.GATSBY_ASSETS_URL}dynamic-import.js`
// var gameRoot = require(`${process.env.GATSBY_GAME_PATH}dynamic-import.js`);

// console.log("index page process?");
// console.log(process.env.GATSBY_ASSETS_URL);
const IndexPage = ({
  data,
}) => {
  const { allJsonJson } = data; // data.markdownRemark holds our post data
  const nodes = allJsonJson.edges;
  console.log("index nodes");
  console.log(nodes);
  const posts = nodes.map(node => {
    return <p key={node.node.id} >
    <Link key={node.node.id} to={node.node.path}>Post: {node.node.value}</Link>
    </p>
    // <PostLink key={node.node.id} post={edge.node} />
  })

  // console.log("MyMod");
  // console.log("env path " + process.env.GATSBY_ASSETS_URL);
  // // let result = getScript("a");
  //  console.log(gameRoot);
   //gameRoot.default.numtoword(5);
  // console.log(result);
  // const Posts = edges
  //   .filter(edge => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
  //   .map(edge => <PostLink key={edge.node.id} post={edge.node} />);

  return <div>
    <blockquote>
        <p>
          There is indeed an element of luck, and no, there isn’t. The prepared
          mind sooner or later finds something important and does it. So yes, it
          is luck.{" "}
          <em>
            The particular thing you do is luck, but that you do something is
            not.
          </em>
        </p>
      </blockquote>
    <button onClick={()=>{
       // gameRoot.default.numtoword(5);
    }}>import</button>
    {/* <Link to="/page-2/">Go to page 2</Link> */}
    <a href="/page-2/">go page 2</a>
    <PostList posts={nodes} />
  </div>;
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexQuery {
    site{
      siteMetadata{
        title
      }
    },
    allJsonJson{
      edges {
        node {
          id
          path
          value
        }
      }
    }
  }
`;


